import DeepData from "../../../assets/js/formate.js";
export default {
  data() {
    return {
      activeName: "first",
      ruleForm: {
        cost: "",
        max:"",
        min:"",
        sit_coins_limit: "",
        min_bet: "",
        person_limit: "",
        loong_limit: "",
        phoenix_limit: "",
        equal_limit: "",
        difference_limit: "",
        all_equal_limit:"",
        type_id: "",
        name: "",
        open_game: "",
        open_robot: "",
        robot_type:"",
        is_hundred_game: "",

      },
      rules: {
        name: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        open_game: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        open_robot: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        cost: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        max: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        min: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        sit_coins_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        min_bet: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        person_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        loong_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        phoenix_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        equal_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        difference_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        all_equal_limit: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
      },
        //房间配置的所有数据
      allData:{},
      //当前游戏房间的所有数据
      currentlist:{},
      //匹配当前游戏的条件
      namelist:['200300','200301','200302','200303','200304','200305'],
      //游戏场次类别
      labellist:[],
      id: 0,
      keys: "",
      loading: false,
    };
  },

    async created() {
       let { data } = await this.$http.HallFunConfig.GetServerConfig({
      key: "roomdata.lua"
    });
    // console.log(data);
     this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.allData = res;
    this.namelist.forEach((item,index)=>{
      Object.keys(res).forEach((it)=>{
        if(item === it){
          this.currentlist[item] = res[it]
          this.currentlist[item].person_limit = res[it].person_limit.replace(/\{|}/g,'')
          this.labellist.push(res[it].name)
        }
        if(index === 0){
          this.activeName = item
          this.ruleForm = res[item]
        }
      })
    })
    // console.log(this.allData,this.currentlist,this.ruleForm,this.labellist);
  },

  methods: {

    handleClick(tab) {
      this.namelist.forEach((item)=>{
        if(item === tab.name){
          this.ruleForm = this.currentlist[item]
        }
      })
    },
    submitForm(formName,type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          // console.log(this.allData,this.currentlist,this.ruleForm);
          let resData = DeepData(this.allData);
          this.namelist.forEach((item)=>{
            Object.keys(resData).forEach((it)=>{
              if(item === it){
                resData[it].person_limit = `{${resData[item].person_limit}}`
              }
            })
          })
          // console.log(this.allData,this.currentlist,this.ruleForm,resData);
          
          if(type === 1){
            //put
                 let { data } = await this.$http.HallFunConfig.PutServerConfig({
              keys: this.keys,
              values: JSON.stringify(resData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }
          }else if(type === 2){
            //post
             this.loading = true;

            let { data } = await this.$http.HallFunConfig.PostServerConfig({
              keys: this.keys,
              values: JSON.stringify(resData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            }
          }
        }else{
           console.log("error submit!!");
          return false;
        }
      })
    },
  }
};



